.about {
  padding: 40px 0;
  @include onTablet {
    padding: 55px 0;
  }
  @include onDesktop {
    padding: 70px 0;
  }
  @include ondesing {
    padding: 100px 0;
  }
  .padimage2 {
    overflow: hidden;
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 290px;
    transition: transform 0.4s;
    margin-bottom: 12px;
    @include onTablet {
      max-height: 290px;
    }
    &:hover {
      transform: scale(1.06);
    }
    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }
  }
  .img2 {
    height: 290px;
    width: 622px;
    object-fit: cover;
    object-position: center;
    position: absolute;
    transition: all 0.3s;
  }
  .about__list1 {
    display: flex;
    flex-direction: row;
    justify-content: left;
    gap: 100px;
    padding-bottom: 10px;
    @include onTablet {
      padding-bottom: 100px;
    }
    @include onDesktop {
      padding-bottom: 190px;
    }
  }
  .about__item1 {
    display: block;
    text-decoration: none;
    font-size: 16px;
    line-height: 130%;
    font-weight: 500;
    font-style: normal;
    color: $grantext;
    cursor: pointer;
    text-align: center;
    border-bottom: 2px solid transparent;
    &:hover {
      font-weight: 500;
      opacity: 0.6;
      border-bottom: 2px solid white;
    }
  }
