  .colorH {
    color: #05c2df;
  }
  .abreviation1 {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    padding: 0 0 20px;
    @include onTablet {
      display: none;
    }
  }
  .abreviation2 {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    padding-bottom: 30px;
  }
  .abreviation {
    display: none;
    @include onTablet {
      display: flex;
      flex-direction: row;
      align-items: baseline;
      padding-bottom: 30px;
    }
  }
  .styleH2d__about {
    font-weight: 900;
    line-height: normal;
  }
  .styleH2d__about1 {
    color: var(--Contrast-Color, #05c2df);
    font-weight: 400;
    line-height: normal;
  }
  .padbot {
    padding-bottom: 30px;
  }
  .buttonLogo {
    display: flex;
    width: 200px;
    height: 70px;
    background-repeat: no-repeat;
    transition: all 0.3s;
    background-image: url(../images/buttonVideo.png);
    padding-bottom: 50px;
    @include onDesktop {
      padding: 0;
    }
    &:hover {
      transition: 0.5s;
      transform: scale(1.07);
    }
    &:focus {
      background-image: url(../images/buttonVideoH.png);
    }
  }
  .header__titleG {
    padding-top: 50px;
  }
  .padGroup5 {
    display: none;
    overflow: hidden;
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 354px;
    transition: transform 0.4s;
    @include onTablet {
      display: flex;
    }
  }
  .png2 {
    height: 354px;
    width: 1175px;
    object-fit: cover;
    object-position: center;
    position: absolute;
    transition: all 0.3s;
  }
  .png2A {
    height: 354px;
    width: 385px;
    object-fit: cover;
    object-position: center;
    position: absolute;
    transition: all 0.3s;
  }
